<template>
  <v-virtual-scroll
    :items="working"
    :item-height="270"
    :max-height="'98%'"
  >
    <template v-slot="{ item }">
      <v-timeline align-top :dense="false" class="resume-working-line">
        <v-timeline-item
          fill-dot
          :right="item.index | cookPosition('right')"
          :left="item.index | cookPosition('left')"
          :color="item.color"
          :icon="item.icon">
          <v-card dark max-width="600" @click="projectClickEvent(item.index)" :style="item.index | cookMargin">
            <v-toolbar flat :color="item.color" dark tile>
              <v-card-title style="text-align:right">{{ item.projectName }}</v-card-title> 
            </v-toolbar>
            <v-card-subtitle class="resume-working-time-text">
              <i style="color:#EF9A9A;margin-right:20px">{{ item.projectRole }}</i>
              {{ item.time }}
            </v-card-subtitle>
            <v-card-text class="white text--primary">
              <v-chip-group column active-class="primary--text">
                <v-chip color="#1B5E20" outlined pill v-for="skill in item.projectSkill" :key="skill.name">
                  <p class="resume-skill-tag">{{ skill.name }}</p>
                  <v-avatar v-if="skill.icon"><v-img :src="skill.icon"></v-img></v-avatar>
                </v-chip>
              </v-chip-group>
            </v-card-text>
          </v-card>
        </v-timeline-item>
      </v-timeline>
    </template>
  </v-virtual-scroll>
</template>

<script>
  import CONFIG from '@/config/personalInfo.js';

  export default {
    data(){
      return {
        working: CONFIG.recentProjects,
      }
    },
    filters:{
      cookMargin(i){
        if(i%2 == 1){
          return "margin-left:30px"
        }
        return ""
      },
      cookPosition(i, type){
        if(i%2 == 1){
          if(type === 'left'){
            return true;
          }else{
            return false;
          }
        }else{
          if(type === 'left'){
            return false;
          }else{
            return true;
          }
        }
      }
    },
    methods:{
      projectClickEvent(index){
        this.$emit('cardClick', index);
      }
    }
  }
</script>

<style lang="stylus" scoped>
.resume-working-time-text
  color  #E0E0E0
  margin-left  20px
  font-size  12px

.resume-working-line
  height 100%
  max-width  50%
  left 20%
  top 36px

.resume-skill-tag
  color #B2FF59
  margin-right  5px

.resume-working-div
  width  300px
  border  3px solid #B0BEC5

</style>